'use client';

import React, { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import 'chart.js/auto';
import { Search, Filter, Edit, Save, Trash2 } from 'lucide-react';

const dummyUsers = Array.from({ length: 30 }).map((_, i) => ({
  id: i + 1,
  name: `user00${i + 1}`,
  role: i % 3 === 0 ? 'A' : 'U',
  surname: 'Zajaceva',
  email: `user${i + 1}@example.com`,
  phone: '+34 11111111',
  address: 'Av. de Madrid',
  city: 'Jaen',
  subscription: i % 2 === 0 ? 'VIP' : 'Standard',
}));

const chartData = {
  labels: ['2019', '2020', '2021', '2022', '2023', '2024', '2025'],
  datasets: [
    {
      label: 'Users per year',
      backgroundColor: '#33b5aa',
      borderRadius: 5,
      data: [5, 10, 15, 20, 30, 45, 60],
    },
  ],
};

const chartOptions = {
  responsive: true,
  plugins: {
    legend: { display: false },
  },
};

export default function AdminMainPage() {
  const [selectedUserId, setSelectedUserId] = useState(null);
  const [showFilter, setShowFilter] = useState(false);
  const [filterRoles, setFilterRoles] = useState([]);
  const [filterSubscriptions, setFilterSubscriptions] = useState([]);
  const [isEditing, setIsEditing] = useState(false);
  const [editableUser, setEditableUser] = useState(null);

  const toggleFilter = (filter, setter) => {
    setter((prev) =>
      prev.includes(filter) ? prev.filter((f) => f !== filter) : [...prev, filter]
    );
  };

  const filteredUsers = dummyUsers.filter(
    (user) =>
      (filterRoles.length === 0 || filterRoles.includes(user.role)) &&
      (filterSubscriptions.length === 0 || filterSubscriptions.includes(user.subscription))
  );

  const selectedUser = dummyUsers.find((u) => u.id === selectedUserId);

  const handleEdit = () => {
    setIsEditing(true);
    setEditableUser({ ...selectedUser });
  };

  const handleSave = () => {
    setIsEditing(false);
  };

  return (
    <div className="flex h-screen bg-[#fdf9f3] overflow-hidden">
      {/* Список пользователей */}
      <div className="w-[280px] p-4 border-r border-gray-200 flex flex-col">
        <div className="relative mb-4">
          <input
            type="text"
            placeholder="Search"
            className="w-full pl-8 pr-10 py-2 rounded bg-gray-100 text-sm"
          />
          <Search size={16} className="absolute left-2 top-2.5 text-gray-400" />
          <Filter
            size={16}
            className="absolute right-2 top-2.5 text-gray-400 cursor-pointer"
            onClick={() => setShowFilter(!showFilter)}
          />
          {showFilter && (
            <div className="absolute top-10 left-0 w-full bg-white shadow rounded p-3 text-sm z-10">
              <div className="mb-2">
                <div className="font-semibold mb-1">Role</div>
                {['A', 'U'].map((role) => (
                  <label key={role} className="block mb-1">
                    <input
                      type="checkbox"
                      className="mr-2"
                      checked={filterRoles.includes(role)}
                      onChange={() => toggleFilter(role, setFilterRoles)}
                    />
                    {role === 'A' ? 'Admin' : 'User'}
                  </label>
                ))}
              </div>
              <div>
                <div className="font-semibold mb-1">Subscription</div>
                {['VIP', 'Standard'].map((type) => (
                  <label key={type} className="block mb-1">
                    <input
                      type="checkbox"
                      className="mr-2"
                      checked={filterSubscriptions.includes(type)}
                      onChange={() => toggleFilter(type, setFilterSubscriptions)}
                    />
                    {type}
                  </label>
                ))}
              </div>
              <button
                onClick={() => {
                  setFilterRoles([]);
                  setFilterSubscriptions([]);
                }}
                className="w-full mt-3 bg-gray-200 text-black rounded py-1"
              >
                Clear all
              </button>
            </div>
          )}
        </div>

        <div className="overflow-y-auto flex-1 pr-1">
          {filteredUsers.map((user) => (
            <div
              key={user.id}
              onClick={() => {
                setSelectedUserId(user.id);
                setIsEditing(false);
              }}
              className={`flex items-center justify-between px-2 py-2 mb-1 rounded cursor-pointer ${
                selectedUserId === user.id ? 'bg-gray-300' : 'bg-gray-200'
              }`}
            >
              <div className="flex items-center gap-2">
                <img
                  src="/images/user.jpg"
                  className="w-6 h-6 rounded-full"
                  alt="avatar"
                />
                <span className="text-sm">{user.name}</span>
              </div>
              <div className="flex gap-1 text-xs">
                <div className="px-2 py-0.5 bg-white rounded">{user.role}</div>
                <div className="px-2 py-0.5 bg-white rounded">{user.subscription}</div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Правая часть */}
      <div className="flex-1 p-4 flex flex-col gap-4">
        {/* График */}
        <div className="h-[220px] bg-white rounded shadow p-4">
          <Bar data={chartData} options={chartOptions} />
        </div>

        {/* Детали пользователя */}
        <div className="bg-white rounded shadow p-4 flex-1">
          {selectedUser ? (
            <div>
              <div className="flex justify-between items-start mb-4">
                <div className="flex items-center gap-4">
                  <img
                    src="/images/user.jpg"
                    className="w-16 h-16 rounded-full"
                    alt="avatar"
                  />
                  <div>
                    <div className="font-bold text-lg">{selectedUser.name}</div>
                    <div className="text-sm text-gray-600">{selectedUser.email}</div>
                  </div>
                </div>
                <div className="flex gap-2">
                  {isEditing ? (
                    <Save className="cursor-pointer" onClick={handleSave} />
                  ) : (
                    <Edit className="cursor-pointer" onClick={handleEdit} />
                  )}
                  <Trash2 className="cursor-pointer" />
                </div>
              </div>
              <div className="grid grid-cols-2 gap-3 text-sm">
                {['surname', 'phone', 'address', 'city', 'subscription'].map((field) => (
                  <div key={field}>
                    <label className="block text-gray-600 capitalize">{field}</label>
                    <input
                      disabled={!isEditing}
                      value={editableUser?.[field] || selectedUser[field]}
                      onChange={(e) =>
                        setEditableUser({ ...editableUser, [field]: e.target.value })
                      }
                      className="w-full border border-gray-300 rounded px-2 py-1"
                    />
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <p className="text-center text-gray-500">Select a user to view details</p>
          )}
        </div>
      </div>
    </div>
  );
}

